import React from "react";
import axios from "axios";
import Remove from "./Remove";

async function Decrement(obj, mail) {
  try {
    const resdata = await axios.get(
      `https://ecommerce-application-95e56-default-rtdb.firebaseio.com/cart${mail}.json`
    );

    console.log("res", resdata.data);

    const indexOfItem = resdata.data.findIndex((preind) => {
      return obj.id === preind.id;
    });

    console.log(indexOfItem);

    const itemQuantity = resdata.data[indexOfItem].quantity - 1;
    if (itemQuantity > 0) {
      const idOfElement = resdata.data[indexOfItem]._id;
      const obj1 = {
        ...obj,
        quantity: itemQuantity,
      };
      console.log(obj1);

      const res = await axios.put(
        `https://ecommerce-application-95e56-default-rtdb.firebaseio.com/cart${mail}/${idOfElement}.json`,
        obj1
      );
      console.log("patch");
    } else {
      Remove(obj.id, mail);
    }
  } catch (err) {
    alert("Somthing went wrong");
  }
}

export default Decrement;
